body {
  margin     : 0;
  padding    : 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing : antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color       : white
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  text-align: center;
}

.app-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height   : 40vmin;
}

.app-header {
  background-color: #282c34;
  min-height      : 100vh;
  display         : flex;
  flex-direction  : column;
  align-items     : center;
  justify-content : center;
  font-size       : calc(10px + 2vmin);
  color           : white;
}

.app-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header{
    background-color: #e9203d;
    color           : white;
    padding         : 15px 10px;
    padding-left    : 0px;
    text-align      : center;
    font-size       : 16px;
    line-height     : 1;
    /* 设置了固定后，下面的子组件就要距离上面一定的距离了 */
    position: fixed;
    width   : 100%;
    z-index : 100;
}
.orderItem{
    background-color: white;
    display         : flex;
    flex-direction  : column;
    border-bottom   : 1px solid white;
    margin          : auto 8px;
}
.orderItem__containner{
    display: flex;
}

.orderItem__picContainner{
    height: 100px;
    width : 100px;
   
}
.orderItem__pic{
    height: 100%;
    width : 100%;
}
 .orderItem__content{
    text-align : left;
    text-indent: 0.5em;
 }
 .orderItem__product{
    background-color: white;
    font-size       : 20px;
    margin          : 12px 0px;
 }
 .orderItem__shop{
    font-size: 13px;
    color    : gray;
    margin   : 12px 0px;
 }
 .orderItem__price{
    font-size: 13px;
    color    : red;
    font-size: 16px;
 }
 .orderItem__btn{
    position  : absolute;
    right     : 10px;
    margin-top: -25px;
   
 }
 .orderItem button{
     color  : white;
     border : 0;
     padding: 1px 25px;
     cursor : pointer;
     z-index: 1;
 }
 .orderItem__btn--red{
    background-color: red;
 }
 .orderItem__btn--gray{
    background-color: #ccc;
 }

 .orderItem__commentContainer{
    display       : flex;
    flex-direction: column;
    text-align    : left;
    padding-bottom: 20px;

 }
 .orderItem__comment{
    min-height : 80px;
    resize     : vertical;
    margin-top : 3px;
    font-size  : 16px;
    text-indent: 0em ;
    border     : #ccc 1px solid;

 }
 .orderItem__StarContainer{
    margin: 3px 0;
 }
 .orderItem__star{
     font-size: 22px;
     cursor   : pointer;
     color    : #ccc;
 }
 .orderItem__star--light{
    color: red;
 }
.orderList{
    padding-top: 47px;
    /* 似乎结构不够合理 */
}
